export default {
  name: 'UserWrapper',
  async asyncData({$axios, params, error}) {
    const { username } = params
    try {
      return await $axios.$get(`/users/${username}`)
    } catch (e) {
      return error(e)
    }
  },
  head() {
    return {
      title: this.user.username
    }
  },
  computed: {
    links() {
      return [
        {
          text: 'Favorit',
          icon: 'heart',
          to: `/users/${this.$route.params.username}`
        },
        {
          text: 'Diikuti',
          icon: 'users',
          to: `/users/${this.$route.params.username}/followings`
        }
      ]
    }
  },
}
